body {
  margin: 0;
  height: 100vh;
  background: linear-gradient(116.82deg, rgba(62, 180, 137, 0.45) 0%, #7851A9 100%) no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    display: flex;
}

.colour-settings{
    position: absolute;
    left: 55%;
    top: 15px;
}
.input-field {
  font-family: "Quicksand", sans-serif;
  border: 2px solid black;
  border-radius: 5px;
  height: 45px;
  font-size: 25px;
  padding-left: 10px;
}

.input-field:focus {
  outline: none;
}

.colour-picker {
  -webkit-animation: showColourPicker 200ms forwards;
          animation: showColourPicker 200ms forwards;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
}

@-webkit-keyframes showColourPicker {
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
  }
}

@keyframes showColourPicker {
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
  }
}

