.drop-menu-container {
  width: 550px;
}
.drop-menu-container .header {
  font-family: "Press Start 2P", cursive;
  font-size: 25px;
  cursor: pointer;
}

.field {
  display: grid;
  grid-template-columns: 200px 200px;
  margin-top: 10px;
  position: relative;
}

.fields {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.label {
  font-family: "Quicksand", sans-serif;
  font-size: 18px;
}

.show {
  -webkit-animation: showItems 200ms linear forwards;
          animation: showItems 200ms linear forwards;
}

.hide {
  -webkit-animation: hideItems 200ms linear forwards;
          animation: hideItems 200ms linear forwards;
}

.show-fields {
  -webkit-animation: showFields 300ms linear forwards;
          animation: showFields 300ms linear forwards;
}

.hide-fields {
  -webkit-animation: hideFields 300ms linear forwards;
          animation: hideFields 300ms linear forwards;
}

@-webkit-keyframes showItems {
  from {
    visibility: hidden;
    opacity: 0;
    left: 0px;
  }
  to {
    opacity: 1;
    left: 20px;
  }
}

@keyframes showItems {
  from {
    visibility: hidden;
    opacity: 0;
    left: 0px;
  }
  to {
    opacity: 1;
    left: 20px;
  }
}
@-webkit-keyframes hideItems {
  from {
    opacity: 1;
    left: 0;
  }
  to {
    visibility: collapse;
    opacity: 0;
    left: -30px;
  }
}
@keyframes hideItems {
  from {
    opacity: 1;
    left: 0;
  }
  to {
    visibility: collapse;
    opacity: 0;
    left: -30px;
  }
}
@-webkit-keyframes hideFields {
  from {
    opacity: 1;
  }
  to {
    height: 0;
    opacity: 0;
    visibility: hidden;
  }
}
@keyframes hideFields {
  from {
    opacity: 1;
  }
  to {
    height: 0;
    opacity: 0;
    visibility: hidden;
  }
}
@-webkit-keyframes showFields {
  from {
    height: 0;
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
  }
}
@keyframes showFields {
  from {
    height: 0;
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
  }
}

