.code-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.code-block-container {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-height: 450px;
  width: 500px;
  background-color: #0F2433;
  border-radius: 10px;
  padding: 15px;
  padding-bottom: 35px;
}

#styles {
  display: flex;
  justify-content: flex-end;
  width: 520px;
}
#styles p {
  margin: 0;
  margin-left: 10px;
  margin-bottom: 5px;
  cursor: pointer;
  font-family: "Quicksand", sans-serif;
}

.selected {
  color: #7851A9;
}

.copy {
  font-family: "Quicksand", sans-serif;
  text-align: right;
  color: white;
  cursor: pointer;
  text-decoration: underline;
  margin: 0;
}

.copy:active {
  color: #cfcfcf;
}

.variables {
  color: white;
}

.style-var {
  margin-left: 20px;
  color: white;
}

