.colour-settings {
  background-color: white;
  height: 600px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 20px;
  overflow: auto;
  scrollbar-color: #dadada white;
  scrollbar-width: thin;
  box-shadow: 0 10px 3.1px rgba(0, 0, 0, 0.017), 0 19.3px 7.4px rgba(0, 0, 0, 0.025), 0 27.9px 13.9px rgba(0, 0, 0, 0.028), 0 36.3px 24.8px rgba(0, 0, 0, 0.03), 0 46.5px 46.4px rgba(0, 0, 0, 0.034), 0 66px 111px rgba(0, 0, 0, 0.05);
}
.colour-settings .header-cs {
  font-family: "Press Start 2P", cursive;
  text-align: center;
  margin: 0;
  margin-bottom: 35px;
}
.colour-settings #background-and-text {
  display: grid;
  grid-template-columns: 220px 220px;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
}
.colour-settings #background-and-text .input-field {
  width: 220px;
  margin-bottom: 5px;
}

.colour-settings::-webkit-scrollbar {
  width: 7px;
}

.colour-settings::-webkit-scrollbar-track {
  background: white;
  margin: 10px;
}

.colour-settings::-webkit-scrollbar-thumb {
  background: #dadada;
  border-radius: 15px;
}

.colour-settings::-webkit-scrollbar-thumb:hover {
  background: #c0c0c0;
}

